<template>
  <div class="mt-10">
    <form
      v-if="key === 'sign-up'"
      class="w-full max-w-screen-sm mx-auto text-center"
      @submit.prevent="handleSubmit"
    >
      <EditAvatar />
      <FormInput
        :placeholder="$t('auth_input_account')"
        :error="accountError"
        :value="account"
        @onChange="handleChangeAccount"
      >
      </FormInput>
      <FormInput
        :placeholder="$t('auth_input_email')"
        :error="emailError"
        :value="email"
        @onChange="handleChangeEmail"
      >
      </FormInput>
      <FormInput
        :placeholder="$t('auth_input_password')"
        :error="passwordError"
        :value="password"
        @onChange="handleChangePassword"
        type="password"
      >
      </FormInput>
      <FormInput
        :placeholder="$t('auth_input_password')"
        :error="repeatPasswordError"
        :value="repeatPassword"
        @onChange="handleChangeRepeatPassword"
        type="password"
      >
      </FormInput>
      <div class="text-xl flex items-center mb-9">
        <Checkbox :value="isAgree" @onChange="handleChangeAgree" />
        {{ $t("agree") }}
        <a href="/system/privacy" target="_blank" class="underline">
          {{ $t("system_privacy") }}
        </a>
        {{ $t("and") }}
        <a href="/system/service" target="_blank" class="underline">
          {{ $t("system_service") }}
        </a>
      </div>
      <button
        class="rounded w-full py-2.5 lg:py-4 text-center text-white bg-button hover:bg-main-red text-xl lg:text-3xl"
      >
        {{ $t("user_sign_up") }}
      </button>
    </form>
    <div class="flex justify-center" v-else>
      <div class="flex items-center gap-8 lg:text-4xl flex-col lg:flex-row">
        <img src="@/assets/mail.png" class="w-20 h-20" />
        {{ $t("sign_up_email") }} <br />
        {{ $t("sign_up_validate") }}
      </div>
    </div>
  </div>
</template>
<script>
import EditAvatar from "@/components/EditAvatar";
import FormInput from "@/components/FormInput";
import Checkbox from "@/components/Checkbox";
export default {
  components: {
    EditAvatar,
    FormInput,
    Checkbox,
  },
  data() {
    return {
      key: "sign-up",
      isAgree: false,
      account: "",
      accountError: "",
      email: "",
      emailError: "",
      password: "",
      passwordError: "",
      repeatPassword: "",
      repeatPasswordError: "",
      isError: false,
    };
  },
  methods: {
    handleChangeAgree(value) {
      this.isAgree = value;
    },
    handleChange(value, field) {
      this[`${field}Error`] = "";
      this[field] = value;
    },
    handleChangeAccount(value) {
      this.handleChange(value, "account");
    },
    handleChangeEmail(value) {
      this.handleChange(value, "email");
    },
    handleChangePassword(value) {
      this.handleChange(value, "password");
    },
    handleChangeRepeatPassword(value) {
      this.handleChange(value, "repeatPassword");
    },

    handleValid(field, errorText) {
      if (!this[field]) {
        this[`${field}Error`] = errorText;
        this.isError = true;
      }
    },
    handleSubmit() {
      this.isError = false;
      this.handleValid("account", `*${this.$t("error_account_exist")}`);
      this.handleValid("email", `*${this.$t("error_email")}`);
      this.handleValid("password", `*${this.$t("error_password")}`);
      this.handleValid("repeatPassword", `*${this.$t("error_password_diff")}`);

      if (this.isError) return;
      console.log(this.account, this.password, this.email);
      this.key = "success";
    },
  },
};
</script>
